.lb_header.fixed {
  background: var(--primary-color) !important;
  padding: 40px 15px;
  border-bottom: 2px solid var(--primary-color) !important;
}



.fixed .lb_logo img {
  /*max-height: auto;*/
  background: black;
  border-radius: 10px;
  padding: 5px 10px;
}

.lb_header.fixed nav ul li a {
  color: black !important;
}

.lb_why .why_ul li {
  border: 2px solid black !important;
  background: var(--primary-color) !important;
}

.lb_why .why_ul li .why_cnt h4 {
  color: black;
}

.lb_why .why_ul li .why_cnt p {
  color: black;
}

.why_icon img {
  background: black;
  padding: 17px;
  border-radius: 100%;
}

.lb_slider {
  background: url('https://lemonbook.in/background-images/1.webp') no-repeat center center !important;
  background-size: cover !important;
}

.lb_walb {
  background: transparent url('https://lemonbook.in/background-images/2.webp') no-repeat center center !important;
  background-size: cover !important;
}

.lb_celeb {
  background: url('https://lemonbook.in/background-images/2.webp') no-repeat center center !important;
  background-size: cover !important;
}

.lb_hiw {
  background: url('https://lemonbook.in/background-images/3.webp') no-repeat center center !important;
  background-size: cover !important;
}

.lb_ue .ue_walink .a_wa {
  color: black;
  border: 2px solid black;
  border-radius: 10px;
  background: var(--sec-color);
  box-shadow: none;
}

.lb_ue .ue_walink .a_wa:hover,
.lb_ue .ue_walink .a_wa:active {
  color: black;
  border: 2px solid black;
  background: white;
}

.lb_slider .mainslider .item .lb_sld_cont .a_id {
  border: 3px solid var(--sec-color);
  box-shadow: 0 0 10px 5px var(--sec-color);
  background: #cb1ac32b;
}

.lb_walb .lb_walb_con .lb_walb_btn .a_di {
  color: black;
  border: 2px solid black;
  border-radius: 10px;
  background: var(--sec-color);
  box-shadow: none;
}

.lb_walb .lb_walb_con .lb_walb_btn .a_di:hover,
.lb_walb .lb_walb_con .lb_walb_btn .a_di:active {
  color: black;
  border: 2px solid black;
  background: white;
}

.lb_hiw .hiw_counter li .hiw_step {
  border: 2px solid white;
  background: var(--sec-color);
  box-shadow: none !important;
}

.lb_hiw .hiw_counter li .hiw_step span {
  color: black;
}

.lb_logo {
  visibility: hidden !important;
}

.lb_why {
  background: url('http://www.lemonbook.in./bg5.webp') no-repeat center center !important;
  background-size: cover !important;
}

@media only screen and (max-width: 766px) {
  .lb_logo {
    visibility: visible !important;
  }
}

.why_cnt {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.lb_why .why_ul li .why_cnt p {
  text-align: justify !important;
}



.aa {
  color: black !important;
  border: 2px solid black;
  padding: 5px;
  border-radius: 10px;
  background: var(--sec-color);
  box-shadow: none;
}



.wh-api {
  z-index: 99999;
  Position: fixed;
  bottom: -20px;
  left: 20px;
}

button.wh-ap-btn {
  outline: none;
  width: 300px;
  height: 150px;
  border: 0;
  background-color: #0000;
  padding: 0;
  border-radius: 0%;
  cursor: pointer;

}

button.wh-ap-btn::after {
  content: '';
  background-image: url(https://www.lemonbook.in/whatsapp.png);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 100%;
  width: 70%;
  height: 100%;
  display: block;
  opacity: 1;
}

button.wh-ap-btn:hover {
  opacity: 1;
  background-color: #0000;
}

@media only screen and (max-width:576px) {

  button.wh-ap-btn::after {
    width: 50%;
  }

  .wh-api {
    bottom: -30px;
  }
}




.lb_why .why_ul li .why_cnt {
  width: max-content !important;
}

.why_cnt {
  align-items: center;
}

.lb_hiw .hiw_wa .wa_link .a_wa {
  color: black;
  border: 2px solid black;
  border-radius: 10px;
  background: var(--sec-color);
  box-shadow: none;
}

.lb_hiw .hiw_wa .wa_link .a_wa:hover,
.lb_hiw .hiw_wa .wa_link .a_wa:active {
