}

.allure-cards {
  width: auto;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 0.0625em 0.0625em, rgba(0, 0, 0, 0.25) 0px 0.125em 0.5em, rgba(255, 255, 255, 0.1) 0px 0px 0px 1px inset;
  border: None;
  margin-top: 10px;
  padding: 20px;
  background: var(--primary-color);
}

.allure-section {
  background: white;
}

.sec-paing {
  padding: 140px 10px;
}

.lb_header {
  padding: 18px 15px;
  /*!important;*/
}

ul,
ol {
  padding-left: 0rem;
}



